import React from 'react';
import { Component } from 'react';
import { Link } from 'react-router-dom';
import aboutMeVideo from '../media/blooper1.mp4';

class Home extends Component {

    state = {
        aboutMeVideoIsActive: false
    }

    render() {
        return (

            <article>
                <main className="jg-main">
                    <section className="selection">
                        <Link className="selection-item" onMouseEnter={this.handleMouseEnterAboutMe} onMouseLeave={this.handleMouseLeaveAboutMe}>About me</Link>
                        <Link className="selection-item" to="/testroute">Test Route</Link>
                    </section>
                    <div className="about-me-video-container">
                        <video id="about-me-video" class={this.state.aboutMeVideoIsActive ? 'active' : ''} ref={(ref) => this.aboutMeVideo = ref} muted>
                            <source src="../media/blooper1.mp4"></source>
                            <source src={aboutMeVideo}></source>
                        </video>
                    </div>
                </main>
            </article>
        )
    }

    handleMouseEnterAboutMe = (e) => {
        this.setState({
            aboutMeVideoIsActive: true
        });
        this.playAboutMeVideo();
    }

    handleMouseLeaveAboutMe = (e) => {
        this.setState({
            aboutMeVideoIsActive: false
        });
    }

    playAboutMeVideo = () => {
        this.aboutMeVideo.currentTime = 0;
        this.aboutMeVideo.play();
    }

}

export default Home;